@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400;600;700;800&display=swap');
:root {
    --primary: #e1dff854;
    --secondary: #202b3b;
    --tertiary: #f0f0f2;
}

body {
    background: linear-gradient(to bottom right, #0f2027, #203a43, #2c5364);
    color: var(--secondary);
    font-family: 'Poppins', sans-serif;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    overflow: hidden;
}

.subContainer {
    border-radius: 1rem;
    background-image: url('./Images/daniil-silantev-dzBo4NIQdIw-unsplash.jpg');
    height: 700px;
    width: 470px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    overflow: hidden;
}

.backdrop {
    height: 100vh;
    flex-direction: column;
    background-color: rgba(94, 91, 91, 0.151);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    padding: 10px;
}

.top {
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: var(--primary);
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
    border-radius: 1rem;
}

.top>input {
    width: 15rem;
    outline: none;
    border: none;
    padding: 10px;
    border-radius: 10px;
}

.cityContainer {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
}
.cityContainer>img {
    width: 10rem;
}

.temp {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
}
.top>h1 {
    font-size: 42px;
}

.bottom {
    margin-top: 15px;
}

.basicDetails {
    display: flex;
    justify-content: space-between;
    gap: 10px;
}

.windSpeed {
    width: 15.5rem;
    height: 10rem;
    padding: 4%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 1rem;
    font-size: 32px;
    font-weight: bold;

    background-color: var(--primary);
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
}

.windSpeed>p {
    font-weight: 400;
    font-size: 18px;
    margin-top: 15px;
}

